.main {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.main-overlay
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.main-title
{
  position: absolute;
  top: 2.9vh;
  left: 2.4vw;
  color: rgba(255, 255, 255, 0.76);
  font-family: "TT Norms-Bold", serif;
  font-size: 3vh;
  text-align: center;
  width: 3vw;
  height: 3vh;
}

.main-content
{
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-content-title
{
