.right-box-input-range {

  -webkit-appearance: none;
  appearance: none;
  position: absolute;
  width: 17vw;
  background-color: rgba(255, 255, 255, 0.06);
  outline: none;
  /*  slider progress trick  */
  overflow: hidden;
  border-radius: 0.5vh;
}

/* Track: webkit browsers */
.right-box-input-range::-webkit-slider-runnable-track {
  height: 1vh;
  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 6vh;
}

/* Track: Mozilla Firefox */
.right-box-input-range::-moz-range-track {
  height: 1vh;
  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 6vh;
}

/* Thumb: webkit */
.right-box-input-range::-webkit-slider-thumb {
  /* removing default appearance */
  -webkit-appearance: none;
  appearance: none;
  /* creating a custom design */
  height: 1vh;
  width: 1vh;
  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 50%;
  border: 0.3vh solid #FC6767FF;
}

/* Thumb: Firefox */
.right-box-input-range::-moz-range-thumb {
  /* creating a custom design */
  height: 1vh;
  width: 1vh;
  background-color: rgba(255, 255, 255, 0.06);
  border-radius: 50%;
  border: 0.1vh solid #FC6767FF;
}

.box-face-settings
{
  position: absolute;
  overflow: auto;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* width */
.box-face-settings::-webkit-scrollbar {
  width: 0.3vw;
}

/* Track */
.box-face-settings::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
.box-face-settings::-webkit-scrollbar-thumb {
  background: #fc6767;
  border-radius: 10px;
}

/* Handle on hover */
.box-face-settings::-webkit-scrollbar-thumb:hover {
  background: #cc5858;
}

